html {
  font-size: 16px;
  color: #000;
}

body {
  font-family: "Roboto", "Arial", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #252525;
}

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}

.header {
  border-bottom: 4px solid #e5493a;
}
.header__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 55px;
  padding-bottom: 55px;
}
.header__logo {
  margin-right: 20px;
}
.header__menu {
  padding: 0;
  margin: 0;
}

@media (max-width: 768px) {
  .header__wrap {
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 25px;
    padding-bottom: 25px;
  }
  .header__logo {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
.menu {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
}
.menu__element {
  margin-right: 35px;
}
.menu__element:last-child {
  margin-right: 0;
}
.menu__link {
  text-decoration: none;
  font-size: 12px;
  font-family: Raleway;
  line-height: 1.667;
  display: inline-block;
  color: #707070;
  transition-duration: 500ms;
  transition-property: color;
}
.menu__link::before {
  content: "";
  width: 8px;
  height: 2px;
  background-color: #e5493a;
  display: block;
  margin-bottom: 2px;
  opacity: 0;
  transition-duration: 500ms;
  transition-property: opacity;
}
.menu__link:hover {
  color: #e5493a;
}
.menu__link:hover::before {
  opacity: 1;
}

@media (max-width: 768px) {
  .menu__element {
    margin-right: 20px;
  }
}