.header {
    border-bottom: 4px solid $second;

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        padding-top: 55px;
        padding-bottom: 55px;
    }

    &__logo {
        margin-right: 20px;
    }

    &__menu {
        padding: 0;
        margin: 0;
    }
}

@media (max-width: 768px) {
    .header {
        &__wrap {
            flex-direction: column;
            justify-content: flex-start;
            padding-top: 25px;
            padding-bottom: 25px;
        }

        &__logo {
            margin-right: 0;
            margin-bottom: 20px;
        }
    }
}